'use strict';

import mongoose from 'mongoose';

var CartSchema = new mongoose.Schema({
  name: String,
   name_ar: String,
  event_date:{type:Date},
  event_start:String,
  event_end:String,
  event_game:String,
  month:String,
  year:String,
  files:Object,
  info: String,
  info_ar: String,
  active: Boolean,
  champion:String,
  user: String,
  status: Number,
  orderNo: String,
  company: {},
  cartItem: {}
});

export default mongoose.model('Cart', CartSchema);
